.button {

    position: relative;
    margin: 10px;
    overflow: hidden;
    cursor: pointer;
}

.button:disabled {
    background: #84b4e7;
    color: #FFFFFF;


    cursor: auto;
    width: 132px;
    height: 30px;
}

.default {
    width: 132px;
    height: 30px;

    background: #0066CC;

    border: 3px solid #4c94de;
    border-radius: 5px;
    color: #FFFFFF;

    padding: 5px 24px 5px 24px;

    gap: 10px;
    outline: none;

}

.red {
    width: 132px;
    height: 30px;

    color: #FFFFFF;
    background: #CC1439;


    border: 3px solid #ce2e4f;
    border-radius: 5px;

}

